.main-banner {
    display: flex;
    justify-content: flex-start;
    padding-top:100px;
    align-items: center;
    flex-grow: 1;
    flex-direction: column;
}
.main-banner-title {
    svg {
        width:1000px;
        max-width:1000px;
    }
}
.main-banner-text {
    margin-top:30px;
    max-width: 840px;
    font-size:19px;
    color: rgba(#fff, .35);
    text-align: center;
    padding-left:20px;
    padding-right: 20px;
}
.main-banner-button {
    margin-top:70px;
    font-size:15px;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    padding:25px 50px;
    border-radius: 40px;
    border:1px solid rgba(#fff, 0.8);
    background: rgba(#fff, 0.08);
    transition: 0.5s all;
}
.main-banner-button:hover {
    background: $color-main;
    color: #222;
    border-color: $color-main;
}

@media (max-width:1060px) {
    .main-banner-title svg {
        width:800px;
    }
}
@media (max-width:820px) {
    .main-banner-title svg {
        width:600px;
    }
}
@media (max-width:760px) {
    .main-banner {
        padding-top:180px;
    }
}
@media (max-width:620px) {
    .main-banner-title svg {
        width:400px;
    }
}
@media (max-width:420px) {
    .main-banner-title svg {
        width:300px;
    }
}

@media (max-height:780px) {
    .main-banner-title svg {
        width:600px;
    }
}