.main-hero {
    $color1 : rgba(black, 0.86);
    $color2 : rgba(black, 0.9);

    height:100vh;
    min-height:700px;
    background:
        linear-gradient(
            -45deg,
            $color1 0%, $color1 25%,
            $color2 25%, $color2 50%,
            $color1 50%, $color1 75%,
            $color2 75%, $color2 100%
        ),
        url(../images/main-banner.jpg);
    background-size: 8px 8px, cover;
    background-repeat: repeat, no-repeat;
    background-position: center bottom;
    display: flex;
    flex-direction: column;
    position: relative;
}

.main-hero-bottom {
    display: flex;
    justify-content: center;
    position: relative;
    overflow: hidden;
}
.main-hero-bottom span:nth-of-type(1),
.main-hero-bottom span:nth-of-type(3) {
    background: #fff;
    position: absolute;
    right:50%;
    margin-right:80px;
    top:0;
    height:100%;
    width:99999px;
}
.main-hero-bottom span:nth-of-type(3) {
    right:auto;
    margin-left:80px;
    margin-right:0;
    left:50%;
}
.main-hero-bottom span:nth-of-type(2) {
    background: url(../images/banner-bottom.svg);
    width:262px;
    height:51px;
    display: block;
}

.main-hero-arrow{
    width:22px;
    height:43px;
    border:1px solid rgba(#fff, 0.8);
    border-radius: 20px;
    position: absolute;
    bottom:60px;
    left:50%;
    transform: translateX(-50%);
}
.main-hero-arrow:before {
    width:6px;
    height:6px;
    border-radius: 50%;
    background: #fff;
    content:'';
    position: absolute;
    left:50%;
    top:5px;
    transform: translateX(-50%);
    animation: heroArrowAnim 0.6s 0s infinite alternate;
}

@keyframes heroArrowAnim {
    0% {top: 5px;}
    100% {top:30px;}
}
